import React, { useState, useEffect } from "react";
import { Edit, Save, X } from "lucide-react";
import "./WeeklyPlanDisplay.css";

// Function to safely convert string to integer
const safeParseInt = (value, defaultValue = 0) => {
  if (value === null || value === undefined || value === "") {
    return defaultValue;
  }
  const parsed = parseInt(value, 10);
  return isNaN(parsed) ? defaultValue : parsed;
};

const calculateProdTonn = (shift1, shift2, shift3, netWt) => {
  const s1 = safeParseInt(shift1);
  const s2 = safeParseInt(shift2);
  const s3 = safeParseInt(shift3);
  const net = safeParseFloat(netWt);

  return Number(((s1 + s2 + s3) * net).toFixed(2)); // Round to 2 decimals
};

// Function to safely convert string to float
const safeParseFloat = (value, defaultValue = 0) => {
  if (value === null || value === undefined || value === "") {
    return defaultValue;
  }
  const parsed = parseFloat(value);
  return isNaN(parsed) ? defaultValue : parsed;
};

// Function to safely convert to string
const safeParseString = (value, defaultValue = "") => {
  if (value === null || value === undefined) {
    return defaultValue;
  }
  return String(value).trim();
};

// Function to validate form data
const validateFormData = (formData) => {
  const errors = [];

  // Required string fields
  if (!safeParseString(formData.prod_order)) {
    errors.push("Production Order is required");
  }

  // Numeric validations
  const plantCode = safeParseInt(formData.plant_code);
  if (plantCode <= 0) {
    errors.push("Plant Code must be a positive number");
  }

  const prodTonn = safeParseFloat(formData.prod_tonn);
  if (prodTonn < 0) {
    errors.push("Production Tonnage cannot be negative");
  }

  // Quantity validations
  const shift1 = safeParseInt(formData.shift1_qty);
  const shift2 = safeParseInt(formData.shift2_qty);
  const shift3 = safeParseInt(formData.shift3_qty);

  if (shift1 < 0 || shift2 < 0 || shift3 < 0) {
    errors.push("Shift quantities cannot be negative");
  }

  return errors;
};

// Function to map API data to component format
const mapApiDataToComponentFormat = (apiData) => {
  return apiData.map((item) => ({
    plantCode:
      item.plant_code !== null &&
      item.plant_code !== undefined &&
      item.plant_code !== 0
        ? item.plant_code
        : "",
    productionOrderNo: item.main_prod_no || "",
    prod_order: item.main_prod_no || "",
    pressId: item.main_forge_press || "",
    customer:
      item.customer_name && item.customer_name !== "None"
        ? item.customer_name
        : "",
    netWt:
      item.week_net_tonn !== null &&
      item.week_net_tonn !== undefined &&
      item.week_net_tonn !== 0
        ? item.week_net_tonn
        : "",
    dieNo: item.week_die_no || "",
    qty: formatQuantity(item.shift1_qty, item.shift2_qty, item.shift3_qty),
    shift1Qty: item.shift1_qty || 0,
    shift2Qty: item.shift2_qty || 0,
    shift3Qty: item.shift3_qty || 0,
    prodTonn:
      item.week_net_tonn !== null &&
      item.week_net_tonn !== undefined &&
      item.week_net_tonn !== 0
        ? item.week_net_tonn
        : "",
    section:
      item.week_section !== null &&
      item.week_section !== undefined &&
      item.week_section !== 0
        ? item.week_section
        : "",
    grade: item.rm_grade && item.rm_grade !== "None" ? item.rm_grade : "",
    dieRequired:
      item.dies_req !== null &&
      item.dies_req !== undefined &&
      item.dies_req !== 0
        ? item.dies_req
        : "",
    rmStatus: item.rm_status && item.rm_status !== "None" ? item.rm_status : "",
    heatCode: item.heat_code && item.heat_code !== "None" ? item.heat_code : "",
    remark: item.remark && item.remark !== "None" ? item.remark : "",
    date: item.week_prod_date,
    rawData: item,
    id: item.id || `${item.main_prod_no}-${item.week_prod_date}`,
  }));
};

//  Keep only one most recent entry per day
const filterLatestPerDay = (data) => {
  const latestByDay = {};

  data.forEach((item) => {
    const dateKey = new Date(item.week_prod_date).toDateString();

    if (!latestByDay[dateKey]) {
      latestByDay[dateKey] = item;
    } else {
      if ((item.id ?? 0) > (latestByDay[dateKey].id ?? 0)) {
        latestByDay[dateKey] = item;
      }
    }
  });

  return Object.values(latestByDay);
};

// Function to format quantity display
const formatQuantity = (shift1, shift2, shift3) => {
  const s1 = shift1 !== null && shift1 !== undefined ? shift1 : 0;
  const s2 = shift2 !== null && shift2 !== undefined ? shift2 : 0;
  const s3 = shift3 !== null && shift3 !== undefined ? shift3 : 0;

  // If all shifts are zero, return empty
  if (s1 === 0 && s2 === 0 && s3 === 0) return "";

  return [s1, s2, s3].join(", ");
};

// Deduplicate by production order or die number (keep latest entry)
const deduplicateLatest = (data) => {
  const uniqueMap = new Map();

  data.forEach((item) => {
    const key = `${item.main_prod_no}-${item.week_prod_date}`; // or use `${item.die_no}-${item.week_prod_date}`
    if (!uniqueMap.has(key)) {
      uniqueMap.set(key, item);
    } else {
      const existing = uniqueMap.get(key);
      // Assuming higher ID means more recent
      if ((item.id ?? 0) > (existing.id ?? 0)) {
        uniqueMap.set(key, item);
      }
    }
  });

  return Array.from(uniqueMap.values());
};

// Function to group data by day of week
const groupDataByDay = (mappedData) => {
  const dayNames = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
  const workingDays = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
  const grouped = {};

  // Initialize all working days
  workingDays.forEach((day) => {
    grouped[day] = [];
  });

  mappedData.forEach((item) => {
    const date = new Date(item.date);
    const dayName = dayNames[date.getDay()];

    if (workingDays.includes(dayName)) {
      grouped[dayName].push({
        ...item,
        dayLabel: `${dayName} (${new Date(item.date).toLocaleDateString(
          "en-GB",
          {
            day: "2-digit",
            month: "short",
          }
        )})`,
      });
    }
  });

  return grouped;
};

// Function to get date range for week title
const getWeekDateRange = (apiData) => {
  if (!apiData.length) return "Loading...";

  const dates = apiData.map((item) => new Date(item.week_prod_date));
  const minDate = new Date(Math.min(...dates));
  const maxDate = new Date(Math.max(...dates));

  const formatDate = (date) => {
    return date.toLocaleDateString("en-GB", {
      day: "2-digit",
      month: "short",
      year: "numeric",
    });
  };

  return `${formatDate(minDate)} - ${formatDate(maxDate)}`;
};

const WeeklyPlanDisplay = () => {
  const [apiData, setApiData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [groupedPlans, setGroupedPlans] = useState({});
  const [weekTitle, setWeekTitle] = useState("Loading...");
  const [editingRow, setEditingRow] = useState(null);
  const [editFormData, setEditFormData] = useState({});
  const [message, setMessage] = useState({ type: "", text: "" });
  const [saving, setSaving] = useState(false);
  const [validationErrors, setValidationErrors] = useState([]);
  const [dieOptions, setDieOptions] = useState([]);
  const [showDieOptions, setShowDieOptions] = useState(false);
  const [matchingDieOptions, setMatchingDieOptions] = useState([]);
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);

  //  FIXED: fetchWeeklyPlan function
  //  FIXED: Add date validation in formatDate function
  const fetchWeeklyPlan = async (start, end) => {
    try {
      setLoading(true);
      setError(null);

      //  FIX: Add proper date validation and conversion
      const formatDate = (date) => {
        // Check if date is valid
        if (!date) {
          console.error(" Date is null/undefined:", date);
          return new Date().toISOString().split("T")[0] + "T00:00:00";
        }

        // Convert to Date object if it's not already
        const dateObj = date instanceof Date ? date : new Date(date);

        // Check if the date is valid
        if (isNaN(dateObj.getTime())) {
          console.error(" Invalid date:", date);
          return new Date().toISOString().split("T")[0] + "T00:00:00";
        }

        return dateObj.toISOString().split("T")[0] + "T00:00:00";
      };

      //  Validate input parameters
      if (!start || !end) {
        console.error(" Start or end date is missing:", { start, end });
        throw new Error("Start and end dates are required");
      }

      const formattedStart = formatDate(start);
      const formattedEnd = formatDate(end);

      const query = `$start_date=${formattedStart} and end_date=${formattedEnd}`;
      const apiUrl = `http://localhost:8080/internal/weekly_plan?${encodeURIComponent(
        query
      )}`;

      console.log(" API Call ", apiUrl);
      console.log(" Date Range:", formattedStart, "to", formattedEnd);
      console.log(" Raw dates:", { start, end });

      const response = await fetch(apiUrl);

      if (!response.ok) {
        const errorText = await response.text();
        console.error(" API Error Response:", errorText);
        throw new Error(
          `HTTP error! status: ${response.status} - ${errorText}`
        );
      }

      const data = await response.json();
      console.log(" API Response received:", data?.length || 0, "records");

      const latestUniqueData = deduplicateLatest(data);
      const latestPerDay = filterLatestPerDay(data);
      const mappedData = mapApiDataToComponentFormat(latestPerDay);
      const grouped = groupDataByDay(mappedData);

      setGroupedPlans(grouped);
      setApiData(latestUniqueData);
      setWeekTitle(
        `Weekly Plan Preview - ${getWeekDateRange(latestUniqueData)}`
      );
    } catch (err) {
      console.error(" Error fetching weekly plan:", err);
      setError(`Failed to load weekly plan: ${err.message}`);
    } finally {
      setLoading(false);
    }
  };

  const getMonday = (date) => {
    if (!date) {
      console.error(" getMonday: date is null/undefined");
      return new Date();
    }

    const d = new Date(date);

    if (isNaN(d.getTime())) {
      console.error(" getMonday: invalid date:", date);
      return new Date();
    }

    const day = d.getDay();
    const diff = day === 0 ? -6 : 1 - day; // Sunday => previous Monday
    d.setDate(d.getDate() + diff);
    return d;
  };

  const handleWeekChange = (direction) => {
    try {
      //  Ensure startDate is a valid Date object
      const currentStart =
        startDate instanceof Date ? startDate : new Date(startDate);

      if (isNaN(currentStart.getTime())) {
        console.error(" Invalid startDate:", startDate);
        // Reset to current week if date is invalid
        const today = new Date();
        const [monday, saturday] = getWeekRange(today);
        setStartDate(monday);
        setEndDate(saturday);
        fetchWeeklyPlan(monday, saturday);
        return;
      }

      const baseDate = new Date(currentStart);
      baseDate.setDate(baseDate.getDate() + direction * 7); // +7 days for next, -7 for prev

      const newStart = getMonday(baseDate);
      const newEnd = new Date(newStart);
      newEnd.setDate(newStart.getDate() + 5); // Monday + 5 = Saturday

      console.log(" Week navigation:", {
        direction,
        currentStart: currentStart.toISOString(),
        newStart: newStart.toISOString(),
        newEnd: newEnd.toISOString(),
      });

      setStartDate(newStart);
      setEndDate(newEnd);

      fetchWeeklyPlan(newStart, newEnd);
    } catch (error) {
      console.error(" Error in handleWeekChange:", error);
      setError(`Failed to change week: ${error.message}`);
    }
  };

  const getWeekRange = (date) => {
    if (!date) {
      console.error(" getWeekRange: date is null/undefined");
      const today = new Date();
      return [today, today];
    }

    const d = new Date(date);

    if (isNaN(d.getTime())) {
      console.error(" getWeekRange: invalid date:", date);
      const today = new Date();
      return [today, today];
    }

    const day = d.getDay();
    const diffToMonday = day === 0 ? -6 : 1 - day;
    const monday = new Date(d);
    monday.setDate(d.getDate() + diffToMonday);
    const saturday = new Date(monday);
    saturday.setDate(monday.getDate() + 5);
    return [monday, saturday];
  };

  useEffect(() => {
    try {
      const today = new Date();
      console.log(" Initializing with today:", today.toISOString());

      const [monday, saturday] = getWeekRange(today);
      console.log(" Initial week range:", {
        monday: monday.toISOString(),
        saturday: saturday.toISOString(),
      });

      setStartDate(monday);
      setEndDate(saturday);
      fetchWeeklyPlan(monday, saturday);
    } catch (error) {
      console.error(" Error in useEffect initialization:", error);
      setError(`Failed to initialize: ${error.message}`);
    }
  }, []);

  useEffect(() => {
    const today = new Date();
    const [monday, saturday] = getWeekRange(today);
    setStartDate(monday);
    setEndDate(saturday);
    fetchWeeklyPlan(monday, saturday);
  }, []);

  // Handle edit button click
  const handleEditClick = (dayName, planIndex, plan) => {
    const rowKey = `${dayName}-${planIndex}`;
    setEditingRow(rowKey);
    setValidationErrors([]);

    setEditFormData({
      prod_order: plan.main_prod_no || "P-000005",
      die_no: plan.dieNo || "DIE456",
      plant_code: plan.plantCode || 2101,
      forge_press: plan.pressId || "FP3000",
      customer: plan.customer_name || "XYZ",
      shift1Qty: item.shift1_qty || 0,
      shift2Qty: item.shift2_qty || 0,
      shift3Qty: item.shift3_qty || 0,
      prod_tonn: plan.prodTonn || 15.5,
      section: plan.section || 90,
      rm_grade: plan.grade || "GR-B",
      die_req: plan.dieRequired || 2,
      rm_status: plan.rmStatus || "Available",
      heat_code: plan.heatCode || "HC001",
      remark: plan.remark || "Ready",
    });
  };

  const handleFieldChange = async (field, value) => {
    setEditFormData((prev) => {
      const updated = { ...prev, [field]: value };

      //  Auto-calculate prod_tonn when related fields are updated
      const shift1 = field === "shift1_qty" ? value : updated.shift1_qty;
      const shift2 = field === "shift2_qty" ? value : updated.shift2_qty;
      const shift3 = field === "shift3_qty" ? value : updated.shift3_qty;
      const netWt = field === "netWt" ? value : updated.netWt || prev.netWt;

      updated.prod_tonn = calculateProdTonn(shift1, shift2, shift3, netWt);

      return updated;
    });

    if (validationErrors.length > 0) {
      setValidationErrors([]);
    }

    //  If the field is die_no, fetch relevant data from API
    if (field === "die_no" && value.trim() !== "") {
      try {
        const res = await fetch(
          `http://localhost:8080/internal/weekly_plan?die_no=${encodeURIComponent(
            value
          )}`
        );
        const data = await res.json();
        const results = data || [];

        console.log("Fetched die records from weekly_entry:", results);

        if (results.length > 0) {
          const first = results[0];
          const shift1 = editFormData.shift1_qty || 0;
          const shift2 = editFormData.shift2_qty || 0;
          const shift3 = editFormData.shift3_qty || 0;
          const netWt = first.week_net_tonn || 0;

          setEditFormData((prev) => ({
            ...prev,
            die_no: first.week_die_no || "",
            plant_code: first.plant_code || "",
            forge_press: first.main_forge_press || "",
            customer: first.customer_name || "",
            prod_order: first.main_prod_no || "",
            netWt: netWt,
            prod_tonn: calculateProdTonn(shift1, shift2, shift3, netWt),
            section: first.week_section || "",
            rm_grade: first.rm_grade || "",
            die_req: first.dies_req || "",
            rm_status: first.rm_status || "",
            heat_code: first.heat_code || "",
            remark: first.remark || "",
          }));

          //  Set matching options only if more than 1 match
          setMatchingDieOptions(results.length > 1 ? results : []);
        } else {
          setMatchingDieOptions([]);
        }
      } catch (err) {
        console.error("Error fetching from weekly_entry:", err);
        setMatchingDieOptions([]);
      }
    }
  };

  const renderWeekHeader = () => {
    const formatWeekDisplay = (start, end) => {
      try {
        if (!start || !end) return "Loading...";

        const startDate = start instanceof Date ? start : new Date(start);
        const endDate = end instanceof Date ? end : new Date(end);

        if (isNaN(startDate.getTime()) || isNaN(endDate.getTime())) {
          return "Invalid Date Range";
        }

        const formatDate = (date) => {
          return date.toLocaleDateString("en-GB", {
            day: "2-digit",
            month: "short",
            year: "numeric",
          });
        };

        return `${formatDate(startDate)} - ${formatDate(endDate)}`;
      } catch (error) {
        console.error(" Error formatting week display:", error);
        return "Error in Date Display";
      }
    };

    return (
      <div className="week-header">
        <button
          onClick={() => handleWeekChange(-1)}
          className="week-nav-btn"
          disabled={loading}
        >
           Previous Week
        </button>

        <h3 className="week-title">
          Weekly Plan Preview - {formatWeekDisplay(startDate, endDate)}
        </h3>

        <button
          onClick={() => handleWeekChange(1)}
          className="week-nav-btn"
          disabled={loading}
        >
          Next Week 
        </button>
      </div>
    );
  };

  // Handle save
  const handleSave = async (dayName, planIndex, originalPlan) => {
    try {
      setSaving(true);
      setMessage({ type: "", text: "" });
      setValidationErrors([]);

      // Validate form data
      const errors = validateFormData(editFormData);
      if (errors.length > 0) {
        setValidationErrors(errors);
        setSaving(false);
        return;
      }

      // Prepare data for API with proper data types
      const updateData = {
        id: originalPlan.id,
        prod_order: safeParseString(editFormData.prod_order),
        die_no: safeParseString(editFormData.die_no),
        plant_code: safeParseInt(editFormData.plant_code, 0),
        forge_press: safeParseString(editFormData.forge_press),
        customer: safeParseString(editFormData.customer),
        shift1_qty: safeParseInt(editFormData.shift1_qty, 0),
        shift2_qty: safeParseInt(editFormData.shift2_qty, 0),
        shift3_qty: safeParseInt(editFormData.shift3_qty, 0),
        prod_tonn: safeParseFloat(editFormData.prod_tonn, 0),
        section: safeParseInt(editFormData.section, 0),
        rm_grade: safeParseString(editFormData.rm_grade),
        die_req: safeParseInt(editFormData.die_req, 0),
        rm_status: safeParseString(editFormData.rm_status),
        heat_code: safeParseString(editFormData.heat_code),
        remark: safeParseString(editFormData.remark),
      };

      console.log("Sending data to API:", updateData);

      // Make API call to update
      const response = await fetch(
        "http://localhost:8080/internal/weekly_plan",
        {
          method: "PATCH",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(updateData),
        }
      );

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`Failed to update: ${response.status} - ${errorText}`);
      }

      //  FIX: Pass current startDate and endDate parameters
      await fetchWeeklyPlan(startDate, endDate);
      setEditingRow(null);
      setEditFormData({});
      setMessage({
        type: "success",
        text: "Plan updated successfully!",
      });

      // Clear message after 3 seconds
      setTimeout(() => {
        setMessage({ type: "", text: "" });
      }, 3000);
    } catch (err) {
      console.error("Error updating plan:", err);
      setMessage({
        type: "error",
        text: `Failed to update plan: ${err.message}`,
      });

      // Clear error message after 5 seconds
      setTimeout(() => {
        setMessage({ type: "", text: "" });
      }, 5000);
    } finally {
      setSaving(false);
    }
  };

  // Handle cancel
  const handleCancel = () => {
    setEditingRow(null);
    setEditFormData({});
    setValidationErrors([]);
  };

  const editableFields = [
    "die_no",
    "forge_press",
    "netWt",
    "customer",
    "prod_order",
    "heat_code",
    "remark",
    "shift1_qty",
    "shift2_qty",
    "shift3_qty",
  ];

  // General field renderer for editable and static view
  const renderValue = (value, field, isEditing, rowKey) => {
    const isEditableField = editableFields.includes(field);

    if (isEditing && editingRow === rowKey && isEditableField) {
      const inputType = ["shift1_qty", "shift2_qty", "shift3_qty"].includes(
        field
      )
        ? "number"
        : "text";

      return (
        <input
          type={
            ["shift1_qty", "shift2_qty", "shift3_qty"].includes(field)
              ? "number"
              : "text"
          }
          className="edit-input"
          value={editFormData[field] || ""}
          onChange={(e) => handleFieldChange(field, e.target.value)}
          placeholder="Enter value"
        />
      );
    }

    if (
      value === null ||
      value === undefined ||
      value === "" ||
      value === "N/A"
    ) {
      return <span className="na-value">N/A</span>;
    }

    if (["netWt", "prodTonn"].includes(field)) {
      const rounded = Math.round(Number(value));
      return isNaN(rounded) ? (
        <span className="na-value">N/A</span>
      ) : (
        rounded.toString()
      );
    }

    return value.toString();
  };

  // Quantity field-specific rendering (like customer dropdown)
  const renderQuantityValue = (plan, isEditing, rowKey, field) => {
    const shouldRenderDropdown =
      isEditing &&
      editingRow === rowKey &&
      matchingDieOptions.length > 0 &&
      ["customer", "forge_press", "plant_code", "prod_order"].includes(field);

    if (shouldRenderDropdown) {
      const labelMap = {
        customer: "Customer",
        forge_press: "Press",
        plant_code: "Plant Code",
        prod_order: "Production Order",
      };

      const uniqueOptions = [
        ...new Set(
          matchingDieOptions.map((item) => {
            if (field === "prod_order") return item.main_prod_no;
            if (field === "forge_press") return item.main_forge_press;
            if (field === "customer") return item.customer_name;
            return item[field];
          })
        ),
      ].filter(Boolean);

      return (
        <select
          className="edit-select"
          onChange={(e) => {
            const selectedValue = e.target.value;
            const matched = matchingDieOptions.find((item) => {
              if (field === "prod_order")
                return item.main_prod_no === selectedValue;
              if (field === "forge_press")
                return item.main_forge_press === selectedValue;
              if (field === "customer")
                return item.customer_name === selectedValue;
              return item[field] === selectedValue;
            });

            if (matched) {
              setEditFormData((prev) => ({
                ...prev,
                die_no: matched.week_die_no || "",
                plant_code: matched.plant_code || "",
                forge_press: matched.main_forge_press || "",
                customer: matched.customer_name || "",
                prod_order: matched.main_prod_no || "",
                prod_tonn: matched.week_net_tonn || "",
                section: matched.week_section || "",
                rm_grade: matched.rm_grade || "",
                die_req: matched.dies_req || 0,
                rm_status: matched.rm_status || "",
                heat_code: matched.heat_code || "",
                remark: "",
              }));

              setMatchingDieOptions([]);
            }
          }}
          value={editFormData[field] || ""}
        >
          <option value="">Select {labelMap[field]}</option>
          {uniqueOptions.map((val, idx) => (
            <option key={idx} value={val}>
              {val}
            </option>
          ))}
        </select>
      );
    }

    //  For shift fields & others: simple input (not dropdown)
    return isEditing && editingRow === rowKey ? (
      <input
        type={
          ["shift1_qty", "shift2_qty", "shift3_qty"].includes(field)
            ? "number"
            : "text"
        }
        className="edit-input"
        value={editFormData[field] || ""}
        onChange={(e) => handleFieldChange(field, e.target.value)}
        placeholder="Enter value"
      />
    ) : plan[field] !== undefined &&
      plan[field] !== null &&
      plan[field] !== "" ? (
      plan[field].toString()
    ) : (
      "N/A"
    );
  };

  //  Correct  wrap in function
  const renderQtyCell = (plan, isEditing, rowKey) => {
    return renderValue(plan.qty, "qty", isEditing, rowKey);
  };

  const renderTableRows = () => {
    const workingDays = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
    const rows = [];

    workingDays.forEach((dayName) => {
      const dayPlans = groupedPlans[dayName] || [];

      //  Skip rendering if no data for the day
      if (dayPlans.length === 0) return;

      dayPlans.forEach((plan, planIndex) => {
        const rowKey = `${dayName}-${planIndex}`;
        const isEditing = editingRow === rowKey;

        rows.push(
          <tr key={rowKey} className={isEditing ? "editing-row" : ""}>
            {planIndex === 0 && (
              <td
                className="weekly-plan-td weekly-day-cell"
                rowSpan={dayPlans.length}
              >
                {plan.dayLabel || dayName}
              </td>
            )}

            <td className="weekly-plan-td">
              {renderValue(plan.plantCode, "plant_code", isEditing, rowKey)}
            </td>

            <td className="weekly-plan-td max-ellipsis-90">
              {renderQuantityValue(plan, isEditing, rowKey, "prod_order")}
            </td>

            <td className="weekly-plan-td">
              {renderQuantityValue(plan, isEditing, rowKey, "pressId")}
            </td>

            <td className="weekly-plan-td customer-cell">
              {renderQuantityValue(plan, isEditing, rowKey, "customer")}
            </td>

            <td className="weekly-plan-td">
              {renderValue(plan.netWt, "prod_tonn", isEditing, rowKey)}
            </td>

            <td className="weekly-plan-td">
              {renderValue(plan.dieNo, "die_no", isEditing, rowKey)}
            </td>

            <td className="weekly-plan-td shift-cell">
              {isEditing
                ? ["shift1_qty", "shift2_qty", "shift3_qty"].map(
                    (field, idx) => (
                      <span key={field} className="shift-edit-wrap">
                        S{idx + 1}:{" "}
                        {renderQuantityValue(plan, isEditing, rowKey, field)}
                      </span>
                    )
                  )
                : ["shift1_qty", "shift2_qty", "shift3_qty"]
                    .map((field, idx) => {
                      const fieldValueMap = {
                        shift1_qty: plan.shift1Qty,
                        shift2_qty: plan.shift2Qty,
                        shift3_qty: plan.shift3Qty,
                      };
                      return `${fieldValueMap[field] ?? 0}`;
                    })
                    .join(", ")}
            </td>

            <td className="weekly-plan-td">
              {renderValue(plan.prodTonn, "prod_tonn", isEditing, rowKey)}
            </td>

            <td className="weekly-plan-td">
              {renderValue(plan.section, "section", isEditing, rowKey)}
            </td>

            <td className="weekly-plan-td">
              {renderValue(plan.grade, "rm_grade", isEditing, rowKey)}
            </td>

            <td className="weekly-plan-td">
              {renderValue(plan.dieRequired, "die_req", isEditing, rowKey)}
            </td>

            <td className="weekly-plan-td">
              {renderValue(plan.rmStatus, "rm_status", isEditing, rowKey)}
            </td>

            <td className="weekly-plan-td">
              {renderValue(plan.heatCode, "heat_code", isEditing, rowKey)}
            </td>

            <td className="weekly-plan-td">
              {renderValue(plan.remark, "remark", isEditing, rowKey)}
            </td>

            <td className="weekly-plan-td action-cell">
              {isEditing ? (
                <div className="flex-gap-2">
                  <button
                    className="action-btn save-btn"
                    onClick={() => handleSave(dayName, planIndex, plan)}
                    disabled={saving}
                    onMouseEnter={(e) => {
                      if (!saving) {
                        e.currentTarget.style.backgroundColor = "#007bff";
                        e.currentTarget.style.color = "white";
                      }
                    }}
                    onMouseLeave={(e) => {
                      if (!saving) {
                        e.currentTarget.style.backgroundColor = "white";
                        e.currentTarget.style.color = "#007bff";
                      }
                    }}
                  >
                    {saving ? "..." : <Save size={10} />}
                  </button>

                  <button
                    className="action-btn cancel-btn"
                    onClick={handleCancel}
                    disabled={saving}
                    onMouseEnter={(e) => {
                      if (!saving) {
                        e.currentTarget.style.backgroundColor = "#dc3545";
                        e.currentTarget.style.color = "white";
                      }
                    }}
                    onMouseLeave={(e) => {
                      if (!saving) {
                        e.currentTarget.style.backgroundColor = "white";
                        e.currentTarget.style.color = "#dc3545";
                      }
                    }}
                  >
                    <X size={10} />
                  </button>
                </div>
              ) : (
                <button
                  className="action-btn"
                  onClick={() => handleEditClick(dayName, planIndex, plan)}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.backgroundColor = "#28a745";
                    e.currentTarget.style.color = "white";
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.backgroundColor = "white";
                    e.currentTarget.style.color = "#28a745";
                  }}
                >
                  <Edit size={12} />
                </button>
              )}
            </td>
          </tr>
        );
      });
    });

    return rows;
  };

  return (
    <div className="weekly-plan-container">
      {renderWeekHeader()}
      {validationErrors.length > 0 && (
        <div className="validation-error">
          <strong>Validation Errors:</strong>
          <ul className="validation-list">
            {validationErrors.map((error, index) => (
              <li key={index}>{error}</li>
            ))}
          </ul>
        </div>
      )}

      {message.text && (
        <div
          className={
            message.type === "success" ? "success-message" : "error-message"
          }
        >
          {message.text}
        </div>
      )}

      <div className="table-container">
        <table className="weekly-plan-table">
          <thead>
            <tr>
              <th className="weekly-plan-th">Day</th>
              <th className="weekly-plan-th">Plant Code</th>
              <th className="weekly-plan-th max-90">Production Order No</th>
              <th className="weekly-plan-th">Press</th>
              <th className="weekly-plan-th">Customer</th>
              <th className="weekly-plan-th">Net Wt</th>
              <th className="weekly-plan-th">Die No</th>
              <th className="weekly-plan-th">Qty (S1, S2, S3)</th>
              <th className="weekly-plan-th">Prod Tonn</th>
              <th className="weekly-plan-th">Section</th>
              <th className="weekly-plan-th">Grade</th>
              <th className="weekly-plan-th">Die Required</th>
              <th className="weekly-plan-th">RM Status</th>
              <th className="weekly-plan-th">Heat Code</th>
              <th className="weekly-plan-th">Remark</th>
              <th className="weekly-plan-th">Action</th>
            </tr>
          </thead>
          <tbody>{renderTableRows()}</tbody>
        </table>
      </div>
    </div>
  );
};

export default WeeklyPlanDisplay;
